'use strict';

import './ArrayExtensions';

import Vue from 'vue';

const setReactiveProperty = (object, key, value) => {
    Vue.set(object, key, value);
};

export class Monitor {

    constructor() {
        this.reset();
    }

    close() {
        this.ws && this.ws.close();
    }

    reset() {
        this.ws = null;
        this.connected = false;
        this.devices = [];
        this.spyConfigs = {};
        this.backlightConfigs = {};
        this.wheelConfigs = {};
        this.thumbWheelConfigs = {};
        this.specialKeys = {};
        this.specialKeyConfigs = {};
        this.events = [];
    }

    connect() {
        this.disconnect();
        this.ws = new WebSocket('ws://localhost:9876');
        this.ws.onopen = this.onOpen.bind(this);
        this.ws.onclose = this.onClose.bind(this);
        this.ws.onerror = this.onError.bind(this);
        this.ws.onmessage = this.onMessage.bind(this);
    }

    disconnect() {
        this.ws && this.ws.close();
    }

    onOpen() {
        console.log('Connected to server');
        this.connected = true;
        this.getDevices();
    }

    onClose() {
        console.log('Disconnected from server');
        this.reset();
    }

    onError(error) {
        console.error(error);
    }

    onMessage(messageJson) {
        const message = JSON.parse(messageJson.data);
        if (message.success) {
            switch (message.verb) {
            case 'event':
                this.onEvent(message.path, message.value);
                break;
            case 'get':
                this.onResponse(message.path, message.value);
                break;
            default:
                break;
            }
        } else {
            console.error(message.value);
        }
    }

    onEvent(path, value) {
        if (path === 'deviceAdded') {
            this.onDeviceAdded(value);
        } else if (path === 'deviceRemoved') {
            this.devices.removeWhere(device => device.unitId === value.unitId);
        } else if (path === 'deviceConnectionChanged') {
            const devices = this.devices.filter(device => device.unitId === value.unitId);
            if (devices.length > 0) {
                const device = devices[0];
                device.isConnected = value.isConnected;
                if (device.isConnected) {
                    this.onDeviceConnected(device);
                }
            } else {
                this.getDevice(value.unitId);
            }
        }

        this.events.push({ name: path, value });
    }

    onDeviceAdded(device) {
        if (device.unitId) { // TOFIX: why do we need this check?
            this.devices.push(device);

            if (device.isConnected) {
                this.onDeviceConnected(device);
            }
        }
    }

    onDeviceConnected(device) {
        if (device.unitId) { // TOFIX: why do we need this check?
            setReactiveProperty(this.spyConfigs, device.unitId, {});
            this.getSpyConfig(device.unitId);

            if (device.hasBacklight) {
                setReactiveProperty(this.backlightConfigs, device.unitId, {});
                this.getBacklightConfig(device.unitId);
            }

            if (device.hasWheel) {
                setReactiveProperty(this.wheelConfigs, device.unitId, {});
                this.getWheelConfig(device.unitId);
            }

            if (device.hasThumbWheel) {
                setReactiveProperty(this.thumbWheelConfigs, device.unitId, {});
                this.getThumbWheelConfig(device.unitId);
            }

            if (device.hasSpecialKeys) {
                setReactiveProperty(this.specialKeys, device.unitId, {});
                setReactiveProperty(this.specialKeyConfigs, device.unitId, {});
                this.getSpecialKeys(device.unitId);
            }

        } else {
            console.error('oops');
        }
    }

    onResponse(path, value) {
        switch (path) {
        case 'devices':
            this.onGetDevices(value);
            break;
        case 'device':
            this.onGetDevice(value);
            break;
        case 'spyConfig':
            this.onGetSpyConfig(value);
            break;
        case 'backlightConfig':
            this.onGetBacklightConfig(value);
            break;
        case 'wheelConfig':
            this.onGetWheelConfig(value);
            break;
        case 'thumbWheelConfig':
            this.onGetThumbWheelConfig(value);
            break;
        case 'specialKeys':
            this.onGetSpecialKeys(value);
            break;
        case 'specialKeyConfig':
            this.onGetSpecialKeyConfig(value);
            break;
        default:
            break;
        }
    }

    getDevices() {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'devices'
        }));
    }

    onGetDevices(devices) {
        for (const device of devices) {
            this.onDeviceAdded(device);
        }
    }

    getDevice(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'device',
            args: { unitId }
        }));
    }

    onGetDevice(device) {
        this.onDeviceAdded(device);
    }

    getSpyConfig(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'spyConfig',
            args: { unitId }
        }));
    }

    onGetSpyConfig(config) {
        this.spyConfigs[config.unitId] = config;
    }

    setSpyConfig(config) {
        this.ws.send(JSON.stringify({
            verb: 'set',
            path: 'spyConfig',
            args: { value: config }
        }));
    }

    getBacklightConfig(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'backlightConfig',
            args: { unitId }
        }));
    }

    onGetBacklightConfig(config) {
        this.backlightConfigs[config.unitId] = config;
    }

    setBacklightConfig(config) {
        this.ws.send(JSON.stringify({
            verb: 'set',
            path: 'backlightConfig',
            args: { value: config }
        }));
    }

    getWheelConfig(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'wheelConfig',
            args: { unitId }
        }));
    }

    onGetWheelConfig(config) {
        this.wheelConfigs[config.unitId] = config;
    }

    setWheelConfig(config) {
        this.ws.send(JSON.stringify({
            verb: 'set',
            path: 'wheelConfig',
            args: { value: config }
        }));
    }

    getThumbWheelConfig(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'thumbWheelConfig',
            args: { unitId }
        }));
    }

    onGetThumbWheelConfig(config) {
        this.thumbWheelConfigs[config.unitId] = config;
    }

    setThumbWheelConfig(config) {
        this.ws.send(JSON.stringify({
            verb: 'set',
            path: 'thumbWheelConfig',
            args: { value: config }
        }));
    }

    getSpecialKeys(unitId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'specialKeys',
            args: { unitId }
        }));
    }

    onGetSpecialKeys(infos) {
        for (const info of infos) {
            setReactiveProperty(this.specialKeys[info.unitId], info.controlId, info);
            this.getSpecialKeyConfig(info.unitId, info.controlId);
        }
    }

    getSpecialKeyConfig(unitId, controlId) {
        this.ws.send(JSON.stringify({
            verb: 'get',
            path: 'specialKeyConfig',
            args: { unitId, controlId }
        }));
    }

    onGetSpecialKeyConfig(config) {
        setReactiveProperty(this.specialKeyConfigs[config.unitId], config.controlId, config);
    }

    setSpecialKeyConfig(config) {
        this.ws.send(JSON.stringify({
            verb: 'set',
            path: 'specialKeyConfig',
            args: { value: config }
        }));
    }
};
